/**
 * The menu is technically the header of the site, it contains the styles for the drop down of the Search Bar.
 *
 */

$lineHeightForNav: 70px;
$lineHeightForSearch: 50px;

header.menu {
  // ...
  width: 100%;
  position: relative;
  z-index: 10;

  div.menu-container {
    width: 100%;
    background-color: #040a2b;
    padding: 0 20px;
    text-align: left;

    div.menu-holder {
      position: relative;
      width: 100%;
      max-width: $siteWidth;
      margin: 0 auto;
    }

    h1 {
      font-family: Arial, Helvetica, sans-serif;
      font-size: 24px;
      line-height: $lineHeightForNav;
      color: white;
      text-align: left;
      display: inline-block;
    }
    nav {
      display: inline-block;
      margin-left: 60px;

      i {
        color: white;
        font-size: 30px;
        line-height: $lineHeightForNav;
      }
      i.search {
        position: absolute;
        right: 10px;
        top: 0;
      }

      a.nav-item {
        font-family: Helvetica, Arial, Sans-Serif;
        font-size: 17px;
        line-height: $lineHeightForNav;
        text-transform: uppercase;
        text-decoration: none;
        white-space: nowrap;
        border-bottom: 1px solid transparent;
        color: #fff;
        margin-right: 20px;
      }
    }
  }

  div.search-container {
    position: absolute;
    background-color: white;
    z-index: 20;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 100%;
    padding: 5px;
    background-color: white;
    border-bottom: 1px solid #ccc;
    display: none;
    &.showing {
      display: block;
    }

    input[type="text"] {
      min-width: 100%;
      font-size: 36px;
      line-height: $lineHeightForSearch;
      border: 1px solid #ccc;
      padding: 0 10px;
    }

    i.close {
      position: absolute;
      right: 10px;
      top: 5px;
      font-size: 36px;
      line-height: $lineHeightForSearch;
    }
    .no-results-wrapper {
      display: flex;
      justify-content: center;
      padding: 20px;
    }
    .result-info {
      color: gray;
      padding: 15px 10px;

      a{
        text-decoration: none;
      }
    }
    .results-wrapper {
      width: 100%;
      padding: 24px 10px;
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;
      gap: 5px;
      background-color: #f5f5f5;

      .result {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 24%;
        padding: 10px;
        align-items: center;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px,
          rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
        background-color: #fff;
        border-radius: 8px;

        &-image {
          display: flex;
          justify-content: center;
          height: auto;
        }
        &-content {
          width: 100%;
          font-family: Arial, Helvetica, sans-serif;
          padding: 10px 10px 15px;
          h4 {
            padding: 10px 0;
          }
          p {
            padding: 10px 0 0;
          }
        }
      }
    }
  }
}
